html {
  scroll-behavior: smooth;
}

body {
  letter-spacing: .03em;
  min-width: 320px;
  cursor: default;
  font-family: DM Sans, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  text-decoration: none;
  list-style: none;
}

img {
  height: auto;
  display: block;
}

